"use client";
import React, { useState } from 'react';
import { Paper, TextField, Button, Grid, Typography } from '@mui/material';
import { Skeleton } from "@/components/ui/skeleton";
import { Heart } from "lucide-react"; // Ícono de corazón de Lucide
import FileUpload from './fileUpload';
import { toast } from "react-toastify";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { createProduct } from '../../utils/supabaseServices'; // Importamos la función para crear productos

const ProductPublish = () => {
  // Estado para controlar los datos del formulario
  const [product, setProduct] = useState({
    name: '',
    description: '',
    price: '',
    fragancia: '',
    intensidad: 1,
    image: '',
    image1: '',
    image2: '',
  });

  // Función para actualizar el estado del formulario
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setProduct((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  // Función para manejar la carga de las imágenes
  const handleImageUpload = (e, imageField) => {
    const file = e.target.files ? e.target.files[0] : null;
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setProduct((prev) => ({
          ...prev,
          [imageField]: reader.result,
        }));
      };
      reader.readAsDataURL(file);
    }
  };

  // Función para manejar la publicación del producto
  const handlePublish = async () => {
    try {
      const productData = await createProduct(product);
      toast.success('Producto publicado con éxito!');
    } catch (error) {
      toast.error('Error al publicar el producto: ' + error.message);
    }
  };

  return (
    <Grid container spacing={2} className="p-8">
      <ToastContainer position="bottom-right" />
      <Grid item xs={12} md={8}>
        <Paper sx={{ padding: 2 }}>
          <Typography variant="h6">Publicar un Producto</Typography>
          <TextField
            fullWidth
            label="Nombre del Producto"
            name="name"
            value={product.name}
            onChange={handleInputChange}
            sx={{ marginBottom: 2 }}
          />
          <TextField
            fullWidth
            label="Descripción"
            name="description"
            value={product.description}
            onChange={handleInputChange}
            sx={{ marginBottom: 2 }}
          />
          <TextField
            fullWidth
            label="Precio"
            name="price"
            value={product.price}
            onChange={handleInputChange}
            sx={{ marginBottom: 2 }}
          />
          <TextField
            fullWidth
            label="Fragancia"
            name="fragancia"
            value={product.fragancia}
            onChange={handleInputChange}
            sx={{ marginBottom: 2 }}
          />
          <TextField
            fullWidth
            label="Intensidad (1-5)"
            name="intensidad"
            type="number"
            value={product.intensidad}
            onChange={handleInputChange}
            inputProps={{ min: 1, max: 5 }}
            sx={{ marginBottom: 2 }}
          />
          {/* Subir imágenes */}
          <FileUpload onFileSelect={(e) => handleImageUpload(e, 'image')} label="Imagen principal" />
          <FileUpload onFileSelect={(e) => handleImageUpload(e, 'image1')} label="Imagen secundaria 1" />
          <FileUpload onFileSelect={(e) => handleImageUpload(e, 'image2')} label="Imagen secundaria 2" />

          <Button
            variant="contained"
            color="primary"
            sx={{ marginTop: 2 }}
            onClick={handlePublish}
          >
            Publicar Producto
          </Button>
        </Paper>
      </Grid>
    </Grid>
  );
};

export default ProductPublish;
